import { Component, Host, h, Prop } from '@stencil/core';

@Component({
  tag: 'ui-cards',
  styleUrl: 'ui-cards.css',
  shadow: true,
})
export class UiCards {
  /** Optional title for the card */
  @Prop() title: string = "Card";

  render() {
    return (
      <Host>
        <div class="card">
          {this.title && (
            <div class="card-header">
              <h3>{this.title}</h3>
            </div>
          )}
          <div class="card-content">
            <slot></slot>
          </div>
        </div>
      </Host>
    );
  }
}
